@font-face {
  font-family: van-icon-result;
  src: url('data:font/truetype;charset=utf-8;base64,')
    format('truetype');
  font-weight: normal;
  font-style: normal;
}

.van-icon-result {
  /* stylelint-disable-next-line font-family-no-missing-generic-family-keyword */
  font-family: van-icon-result !important;
  font-size: 32px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.van-icon-result-warning::before {
  content: '\ec72';
}

.van-icon-result-info::before {
  content: '\e6ce';
}

.van-icon-result-wait::before {
  content: '\e743';
}

.van-icon-result-success::before {
  content: '\e68b';
}

.van-icon-result-error::before {
  content: '\e6cb';
}
